@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
.home {
  min-height: 100vh;
  padding: 20px;
  background-color: var(--primary-color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.home .logo {
  margin: auto;
  color: white;
}
.logo div {
  width: 320px;
  height: 240px;
  background-position: center;
  background-size: cover;
}

.home .button {
  background-color: white;
  color: var(--primary-color);
  margin-bottom: 40px;
  padding: 15px;
  border-radius: 12px;
  box-shadow: 2px 2px 10px #6C3C24;
  transition: 0.5s;
  font-family: 'Quicksand', sans-serif;
  font-size: large;
  font-weight: 700;
  text-decoration: none;
}

.home .button:hover {
  cursor: pointer;/**/
}

.home .button:active {
  transform: scale(0.98);
}

.home .button i {
  padding-left: 10px;
  transform: scale(1.4);
}

.home .networks {
  width: 40%;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}

.home .networks a i {
  transform: scale(1.2);
  color: white;
}

.home .networks i:hover {
  cursor: pointer;/**/
}

.home .networks i:active {
  transform: scale(1.15);
}
.layout {
  min-height: 100vh;
}

.layout .logo {
  position: absolute;
  top: 10px;
  left: 10px;
}

.layout .logo div {
  width: 110px;
  height: 82px;
  background-position: center;
  background-size: cover;
}
.menu {
  height: 100vh;
  font-family: 'Quicksand', sans-serif;
}

.menu .title {
  position: absolute;
  top: 35px;
  right: 10%;
  margin: auto;
  width: 50%;
  align-items: center;
  font-size: large;
  font-weight: 700;
}

.menu .title h1 {
  font-size: 24px;
  color: var(--secondary-color);
}
/*--------------Filter--------------*/
.menu .form {
  position: absolute;
  top: 30px;
  right: 40px;
  width: 220px;
  box-shadow: 1px 1px 1px 1px rgba(128, 128, 128, 0.4);/*ยก?ยก?*/
  border-radius: 4px;
}

.label {
  border: green;/*ยก---------?*/
  height: 100px;
}

.menu .form input {
  width: 85%;
  border: none;
  padding: 5px;
  height: 30px;
  font-size: 20px;
  background-color: var(--color-sombra-claro-rojo-naranja);
}

.menu .form input:focus {
  outline: none;
}

.menu .form i {
  color: gray;
  transform: scale(1.2);
}

/*----------General Articles-----------*/
.menu .container-general-articles {
  height: 28%;
  padding-top: 90px;
}

.menu .general-articles {
  margin: auto;
  width: 90%;
  display: flex;
  justify-content: center;
  font-size: 12px;
}

.menu .general-articles .arrow {
  display: flex;
  color: var(--secondary-color);
}

.menu .general-articles i {
  margin: auto;
  transform: scale(2);
}

.menu .general-articles .articles {
  display: flex;
  justify-content: space-between;
  width: 80%;
}

.menu .general-articles .articles figure {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(87, 86, 86);
  font-size: 14px;
  font-weight: 600;
}

.menu .general-articles .articles figure:hover {
  cursor: pointer;
}

.menu .general-articles .articles figure div {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
/*-------------List of items--------------*/
.menu .list-of-items {
  height: 63%;
  padding: 40px 0 18px 0;
  overflow-y: scroll;
}

.menu .list-of-items article {
  position: relative;
  width: 80%;
  margin: auto;
  background-color: white;
  box-shadow: 1px 1px 16px 4px rgba(128, 128, 128, 0.4);
  text-align: center;
  border-radius: 12px;
  margin-top: 50px;
}

.menu .list-of-items article.hidden {
  display: none;
}

.menu .list-of-items article:first-child {
  margin-top: 0%;
}

.menu .list-of-items article figure {
  position: absolute;
  top: -40px;
  left: 0;
  right: 0;
  margin: auto;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.menu .list-of-items article .contents {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  min-height: 150px;
  padding-top: 40px;
}

.menu .list-of-items article .contents p:nth-of-type(1) {
  color: gray;
}

.menu .list-of-items article .contents p:nth-of-type(2) {
  font-weight: bold;
}

.menu .list-of-items article .contents p:last-child {
  font-weight: bold;
  font-size: 24px;
}

/*Buttons less & more*/
.menu .list-of-items article .button {
  position: absolute;
  height: 40px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(218, 205, 136);
}

.menu .list-of-items article .button-left {
  bottom: 0%;
  border-radius: 0 12px 0 12px;
}

.menu .list-of-items article .button-right {
  bottom: 0%;
  right: 0%;
  border-radius: 12px 0 12px 0;
}

.menu .list-of-items article .circle {
  border: solid var(--secondary-color);
  height: 25px;
  width: 25px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  color: var(--secondary-color);
}

/*-----------To shopping cart------------*/
.menu > div:last-child {
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu .to-shopping-cart {
  height: 45px;
  width: 100px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: var(--tertiary-color);
  border-radius: 24px;
  color: black;
  text-decoration: none;
  box-shadow: 1px 1px 1px 1px rgba(128, 128, 128, 0.4);
}

.menu .to-shopping-cart:active {
  transform: scale(0.99);
}

.menu .to-shopping-cart i {
  transform: scale(1.5);
}

.menu .to-shopping-cart p {
  height: 35px;
  width: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 50%;
  font-size: 24px;
}
.order {
  height: 100vh;
  font-family: 'Quicksand', sans-serif;
}

.order .title {
  position: absolute;
  top: 35px;
  right: 20%;
  margin: auto;
  width: 40%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: large;
  font-weight: 700;
}

.order .title h1 {
  font-size: 24px;
  color: var(--secondary-color);
}

.order .title i {
  transform: scale(2);
  color: var(--tertiary-color);
}

/*-----------------Alert Message----------------*/
.order .message-container {
  height: 33%;
  display: flex;
  align-items: flex-end;
  padding-bottom: 15px;
}

.order .message {
  width: 90%;
  margin: 0 auto;
  padding: 5px;
  box-shadow: 1px 1px 1px 1px rgba(128, 128, 128, 0.4);
  border-radius: 12px;
}

.order .message h1 {
  color: var(--primary-color);
  padding: 5px;
  font-size: 20px;
  font-weight: 600;
}

.order .message p {
  padding: 5px;
  font-size: medium;
  font-weight: 500;
}

/*-------------Articles shopping Cart-------------*/
.order .articles {
  height: 39%;
  overflow-y: scroll;
  /* display: flex;
  flex-direction: column;
  justify-content: space-evenly; */
  align-items: center;
}

.order .articles .article-container {
  margin: auto;
  width: 100%;
  display: flex;
  justify-content: center;
}

.order .articles article {
  position: relative;
  height: 80px;
  width: 70%;
  margin-bottom: 15px;
  transform: translateX(20px);
  background-color: rgb(233, 220, 145);
  border-radius: 12px;
  text-align: center;
}

.order .articles article .contents {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 0 40px 0 40px;
}

.order .articles article .contents p:nth-of-type(1) {
  font-size: 14px;
  color: var(--color-sombra-oscura-cian);
}

.order .articles article .contents p:nth-of-type(2) {
  font-weight: bold;
}

.order .articles article .contents p:last-child {
  position: absolute;
  right: 0%;
  width: 35px;
  font-size: 16px;
  font-weight: bold;
}

/* .shopping-cart .articles article .contents p:last-child {
  position: absolute;
  right: 0%;
  width: 35px;
  font-size: 20px;
} */

.order .articles article figure {
  position: absolute;
  left: -40px;
  top: 0%;
  bottom: 0%;
  margin: auto;
  height: 80px;
  width: 80px;
  border-radius: 50%;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: #f5f5f5;
}

.order .articles article .button {
  position: absolute;
  cursor: pointer;
}

.order .articles article .circle {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: var(--secondary-color);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
}

.order .articles article .button-left {
  right: 5px;
  bottom: 2px;
}

.order .articles article .button-right {
  right: 5px;
  top: 2px;
}

/*-------------------Resume-------------------*/
.order .resume {
  height: 28%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.order .resume div a {
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: white;
  text-decoration: none;
  border-radius: 12px;
}

.order .resume div:first-child a {
  width: 60%;
  background-color: var(--tertiary-color);
  font-size: 17px;
  font-weight: 600;
}

.order .resume div a p {
  text-align: center;
  padding: 10px;
}

.order .resume > div:nth-of-type(2) .row {
  width: 80%;
  margin: auto;
  padding: 2px;
  display: flex;
  justify-content: space-between;
}

.order .resume > div:nth-of-type(2) .row:last-child {
  font-weight: bold;
}

.order .resume div:last-child a {
  width: 70%;
  background-color: var(--primary-color);
  font-size: 20px;
  font-size: 20px;
  font-weight: 600;
}

.order .resume div:last-child a i {
  transform: scale(1.5);
}
* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

html {
  /* font-size: 62.5%; */
}

:root {
  --primary-color: #E8723E;
  --secondary-color: #6C3C24;
  --tertiary-color: #FBBB3B;
  --quaternary: #0000;
}

body {
  min-height: 100vh;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


